import React from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";

import { Edition } from "../data";
import useEditionData from "../hooks/useEditionData";
import RegisterButton from "./general/registerButton";

import * as styles from "./hackathon.module.css";

type HackathonProps = {
  edition: Edition;
};

type HackathonDetails = {
  title: string;
  content: string;
  extra_details?: {
    text: string;
    link: string;
  };
};

const Hackathon: React.FC<HackathonProps> = ({ edition }) => {
  const { t } = useTranslation();

  const { hackathon: date } = useEditionData(edition);
  const details = t("details", {
    returnObjects: true,
  }) as HackathonDetails[];

  return (
    <div className={`page ${styles.hackathon}`} id={"Hackathon"}>
      <h2 className={"date"}>{date}</h2>
      <h1 className={"title"}>{t("title")}</h1>

      <div className={styles.info}>
        {details.map((item, index) => (
          <div key={"Hackathon" + index}>
            <p className={"p"}>{t(item.title)}</p>
            <p className={"p"}>{t(item.content)}</p>
          </div>
        ))}
      </div>

      <RegisterButton />
    </div>
  );
};

export default Hackathon;
